// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Comfortaa", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  // colors
  --primary: #F0F4FA;

}
